<template>
  <BaseLayout title="话费团充" bg="linear-gradient(180deg, #FFF, #EEE 10%, #FFF 50%, #FFF)">
    <template #extra>
      <van-button icon="todo-list-o" :style="{width:'110px', borderRadius:'10px 0 0 10px', color: '#000'}" color="#F6F6F6" @click="$router.push('rechargerecord')">充值记录</van-button>
    </template>

    <div class="panel">
      <div class="options">
        <van-cell class="goods" v-for="(goods,index) in state.goods" :key="index" :title="goods.face_value + ' 元'" :class="{active: state.id == goods.id}" @click="onChoose(goods)">
          <template #icon></template>
          <template #label><div class="price">售 ￥{{goods.price}}</div></template>
        </van-cell>
      </div>
      <div class="book">
        <van-cell v-for="(book,index) in state.book" :key="index" :title="book.title" :class="{active: state.bid == book.id}" is-link @click="onSelect(book, true)">
          <template #icon><van-icon class="icon" :name="state.bid == book.id ? 'checked' : 'circle'" :class="{active: state.bid == book.id}" @click.stop="onSelect(book, false)"></van-icon></template>
          <template #label>共有 {{book.detail.length}} 人</template>
        </van-cell>
        <van-button class="add" type="default" icon="add-o" block @click="state.modify=true;state.bid=0;state.title='';state.item={}">新增电话簿</van-button>
      </div>
      <div class="form">
        <div class="total" v-show="valid">合计：{{state.price}} * {{state.count}} = {{state.price*state.count}} 元</div>
        <van-button type="danger" block @click="onRecharge">立即充值</van-button>
      </div>
    </div>

    <van-action-sheet v-model:show="state.modify" title="电话簿管理">
      <van-field type="text" v-model="state.title" label="电话簿名称" placeholder="请填写" input-align="right" @change="state.bid >0 && onSave()"></van-field>
      <fieldset class="wrapper" v-show="state.bid > 0">
        <legend class="title">已有号码</legend>
        <van-field type="text" v-model="state.search" placeholder="号码查询" clearable></van-field>
        <div style="max-height: 215px;overflow: scroll;">
          <van-cell v-for="(v,k) in state.item.detail" :key="k" :value="v.name">
            <template #title><span v-html="getTitle(v.mobile)"></span></template>
            <template #extra><van-icon class="remove" name="close" @click="onRemove(k)"></van-icon></template>
          </van-cell>
        </div>
      </fieldset>
      <fieldset class="wrapper">
        <legend class="title">增加号码</legend>
        <van-field type="textarea" v-model="state.append" :autosize="{maxHeight:120, minHeight:90}" placeholder="每个号码一行（如有姓名，可加在号码后面，用逗号、分号或空格隔开）"></van-field>
        <van-button type="danger" block @click="onAdd">增加</van-button>
      </fieldset>
    </van-action-sheet>

  </BaseLayout>
</template>

<style scoped>

.panel {
  margin: 10px;
  border:solid 1px #EEE;
  border-radius: 5px;
  background: #FFF;
}

.options {
  padding: 5px;
  background: #F9F9F9;
  border-radius: 5px 5px 0 0;
  display: flex;
  flex-flow: row nowrap;
  align-content: space-between;
}
.options .price {
  color: #F66;
  font-size: 12px;
}
.goods {
  margin: 5px;
  background: #FFF;
  border: solid 1px #CCC;
  border-radius: 5px;
  overflow: hidden;
}
.goods.active {
  background: #FEE;
  border-color: #F00;
}

.book {
  margin: 10px;
  background: #FFF;
  border-radius: 5px;
}
.icon {
  margin-top: 10px;
  margin-right: 10px;
  color: #666;
  font-size: 24px;
}
.icon.active {
  color: #F00;
}
.add {
  margin-top: 10px;
  background: #F9F9F9;
}

.form {
  position: fixed;
  left: 0px;
  bottom: 0px;
  box-sizing: border-box;
  width: 100%;
  padding: 10px;
}
.total {
  padding: 10px;
  background:#F9F9F9;
  color: #F00;
  font-size: 12px;
  text-align: right;
}

.wrapper {
  margin: 10px;
  padding: 0px;
  border: solid 1px #F3F3F3;
  border-radius: 5px;
}
.wrapper .title {
  padding: 0 5px;
  color: #666;
  font-size: 14px;
}
.wrapper .remove {
  margin: 3px 0 0 20px;
  font-size: 18px;
}
</style>

<script>
import {reactive, ref} from 'vue'
import {mapState, mapGetters, mapMutations} from 'vuex'
import BaseLayout from '@/components/BaseLayout.vue'

export default {
  components: {BaseLayout},

  data() {
    const state = reactive({
      id: 0,
      price: 0,

      bid: 0,
      count: 0,
      item: {},

      modify: false,
      title: "",
      search: "",
      append: "",

      goods: [],
      book: [],
    });
    const list = [];
    return {state, list};
  },

  created() {
    this.init();
  },

  computed: {
    valid() {
      return this.state.id > 0 && this.state.bid > 0;
    },

    ...mapState(["profile"]),
  },

  methods: {
    init() {
      this.$http.get("/digital/list?cid=1&keyword=话费").then((data) => {
        this.state.goods = data;
      });
      this.$http.get("/digital/booklist").then((data) => {
        this.state.book = data;
      });
    },

    /* 主界面相关操作 */
    onChoose(goods) {
      this.state.id = goods.id;
      this.state.price = goods.price;
    },
    onSelect(book, show){
      this.state.item = book;
      this.state.bid = book.id;
      this.state.count = book.detail.length;
      this.state.title = book.title;
      this.state.modify = show;

      this.list = _.clone(book.detail);
    },
    onRecharge(){
      if (!this.valid)
        return this.$toast("请先选择充值金额和电话簿");
      if (this.list.length == 0)
        return this.$toast("无有效的手机号码");
      
      this.recharge();
    },
    recharge() {
      if (this.list.length <= 0){
        this.state.bid = 0;
        return this.$dialog.alert({
          title: "订单生成完毕",
          message: "请点击右上角“充值记录”查看到账情况",
          theme: "round-button",
        });
      }

      let obj = this.list.pop()
      this.$http.get("/digital/recharge?id=" + this.state.id + "&account=" + obj.mobile + "&remark=话费团充（" + this.state.title + "）").then(() => {
        setTimeout(this.recharge, 200);
      });
    },

    /* 弹出电话本相关操作 */
    getTitle(mobile) {
      if (this.state.search.length == 0 || mobile.toString().search(this.state.search) == -1)
        return mobile;
      return mobile.toString().replace(this.state.search, "<b style='color:red;'>" + this.state.search + "</b>");
    },
    onRemove(id) {
      this.state.item.detail.splice(id, 1);
      this.onSave();
    },
    onAdd() {
      if (this.state.bid == 0)
        this.state.item.detail = [];
      let arr = _.filter(this.state.append.replace("\r", "\n").split("\n"));
      for (let i in arr) {
        let item = _.trim(arr[i]);
        let reg = new RegExp("^([\\d]{11})[，；,;\\s]*(.*)$", "i");
        reg.test(item) && this.state.item.detail.push({mobile:item.match(reg)[1], name:item.match(reg)[2]});
      }
      if (this.state.item.detail.length == 0)
        return this.$toast("无有效的手机号码");
      this.onSave();
    },
    onSave() {
      this.$http.post("/digital/booksave?id=" + this.state.bid + "&title=" + this.state.title, {detail: JSON.stringify(this.state.item.detail)}).then((data) => {
        this.state.append = "";
        this.init();
        if (this.state.bid == 0)
          this.state.modify = false;
      });
    },

    ...mapMutations(["auth"]),
  }
}
</script>
